@import "./../Main.scss";

.icon{
    position: absolute;
    width: 17px;
    height: 17px;
    mask-position: center;
    mask-size: contain;
    mask-repeat: no-repeat;
    cursor: pointer;
    opacity: 0.5;
    background-color:$BrightColor;
    top: 50%;
    transform: translateY(-50%);
    transition: opacity 0.2s ease-out; 
   

}
.hidden{
    opacity: 0;


}
.reveal{
    opacity: 1;

}
.title{
    position: absolute;
    font-family: "Assistant-Light";
    font-size: 12pt;
    left: 30px;
    top: 50%;
    transform: translateY(-50%);
    transition: opacity 0.1s ease-out;
}
